'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import * as Dialog from '@radix-ui/react-dialog';
import {
  X,
  Briefcase,
  Building2,
  MapPin,
  Calendar,
  FileText,
  Code2,
  Loader2,
  Plus,
} from 'lucide-react';
import { Button } from '@/components/ui/Button';
import { createExperience } from '@/lib/api/profile';
import type { ExperienceInput, Experience } from '@/types/profile';

interface AddExperienceModalProps {
  isOpen: boolean;
  onClose: () => void;
  onExperienceAdded: (experience: Experience) => void;
}

const experienceTypes = [
  { value: 'Internship', label: 'Internship', color: 'from-blue-500 to-cyan-500' },
  { value: 'Part-time', label: 'Part-time', color: 'from-green-500 to-emerald-500' },
  { value: 'Full-time', label: 'Full-time', color: 'from-purple-500 to-violet-500' },
  { value: 'Freelance', label: 'Freelance', color: 'from-orange-500 to-amber-500' },
  { value: 'Contract', label: 'Contract', color: 'from-pink-500 to-rose-500' },
] as const;

export function AddExperienceModal({
  isOpen,
  onClose,
  onExperienceAdded,
}: AddExperienceModalProps) {
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [skillInput, setSkillInput] = useState('');

  const [formData, setFormData] = useState<ExperienceInput>({
    title: '',
    company: '',
    type: 'Internship',
    location: '',
    startDate: '',
    endDate: '',
    isCurrent: false,
    description: '',
    skills: [],
  });

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleCheckboxChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, checked } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: checked,
      ...(name === 'isCurrent' && checked ? { endDate: '' } : {}),
    }));
  };

  const addSkill = () => {
    if (skillInput.trim() && !formData.skills?.includes(skillInput.trim())) {
      setFormData((prev) => ({
        ...prev,
        skills: [...(prev.skills || []), skillInput.trim()],
      }));
      setSkillInput('');
    }
  };

  const removeSkill = (skill: string) => {
    setFormData((prev) => ({
      ...prev,
      skills: prev.skills?.filter((s) => s !== skill) || [],
    }));
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      addSkill();
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.title.trim()) {
      setError('Job title is required');
      return;
    }
    if (!formData.company.trim()) {
      setError('Company name is required');
      return;
    }
    if (!formData.startDate) {
      setError('Start date is required');
      return;
    }

    setSaving(true);
    setError(null);

    try {
      const experience = await createExperience(formData);
      onExperienceAdded(experience);
      // Reset form
      setFormData({
        title: '',
        company: '',
        type: 'Internship',
        location: '',
        startDate: '',
        endDate: '',
        isCurrent: false,
        description: '',
        skills: [],
      });
      onClose();
    } catch (err: any) {
      setError(err.response?.data?.error || 'Failed to add experience');
    } finally {
      setSaving(false);
    }
  };

  return (
    <Dialog.Root open={isOpen} onOpenChange={onClose}>
      <Dialog.Portal>
        <AnimatePresence>
          {isOpen && (
            <>
              <Dialog.Overlay asChild>
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                  className="fixed inset-0 bg-black/60 backdrop-blur-md z-50"
                />
              </Dialog.Overlay>

              <Dialog.Content asChild>
                <motion.div
                  initial={{ opacity: 0, scale: 0.95, y: 20 }}
                  animate={{ opacity: 1, scale: 1, y: 0 }}
                  exit={{ opacity: 0, scale: 0.95, y: 20 }}
                  transition={{ type: 'spring', damping: 25, stiffness: 300 }}
                  className="fixed top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-2xl max-h-[90vh] z-50"
                >
                  <div className="relative bg-white/90 dark:bg-neutral-900/90 backdrop-blur-xl rounded-2xl border border-white/20 dark:border-neutral-700/50 shadow-2xl overflow-hidden">
                    {/* Gradient accent */}
                    <div className="absolute top-0 left-0 right-0 h-1 bg-gradient-to-r from-emerald-500 via-teal-500 to-cyan-500" />

                    {/* Header */}
                    <div className="flex items-center justify-between p-5 border-b border-gray-200/50 dark:border-neutral-800/50">
                      <div className="flex items-center gap-3">
                        <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-emerald-500 to-teal-600 flex items-center justify-center shadow-lg">
                          <Briefcase className="w-5 h-5 text-white" />
                        </div>
                        <Dialog.Title className="text-xl font-bold text-gray-900 dark:text-white">
                          Add Experience
                        </Dialog.Title>
                      </div>
                      <Dialog.Close asChild>
                        <button className="p-2 text-gray-500 dark:text-neutral-400 hover:text-gray-700 dark:hover:text-white transition-colors rounded-lg hover:bg-gray-100 dark:hover:bg-neutral-800">
                          <X className="w-5 h-5" />
                        </button>
                      </Dialog.Close>
                    </div>

                    {/* Form */}
                    <form onSubmit={handleSubmit} className="overflow-y-auto max-h-[calc(90vh-160px)]">
                      <div className="p-5 space-y-5">
                        {/* Error */}
                        {error && (
                          <motion.div
                            initial={{ opacity: 0, y: -10 }}
                            animate={{ opacity: 1, y: 0 }}
                            className="p-3 bg-red-500/10 border border-red-500/20 rounded-xl text-red-600 dark:text-red-400 text-sm"
                          >
                            {error}
                          </motion.div>
                        )}

                        {/* Job Title */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Briefcase className="w-4 h-4 text-emerald-500" />
                            Job Title *
                          </label>
                          <input
                            type="text"
                            name="title"
                            value={formData.title}
                            onChange={handleChange}
                            placeholder="e.g., Software Engineer Intern"
                            className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-emerald-500/50 focus:border-emerald-500 transition-all"
                          />
                        </div>

                        {/* Company */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Building2 className="w-4 h-4 text-emerald-500" />
                            Company *
                          </label>
                          <input
                            type="text"
                            name="company"
                            value={formData.company}
                            onChange={handleChange}
                            placeholder="e.g., Google, Microsoft, Startup Name"
                            className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-emerald-500/50 focus:border-emerald-500 transition-all"
                          />
                        </div>

                        {/* Employment Type */}
                        <div className="space-y-2">
                          <label className="text-sm font-medium text-gray-700 dark:text-neutral-300">
                            Employment Type *
                          </label>
                          <div className="flex flex-wrap gap-2">
                            {experienceTypes.map((type) => (
                              <button
                                key={type.value}
                                type="button"
                                onClick={() =>
                                  setFormData((prev) => ({ ...prev, type: type.value }))
                                }
                                className={`px-4 py-2 rounded-xl text-sm font-medium transition-all ${
                                  formData.type === type.value
                                    ? `bg-gradient-to-r ${type.color} text-white shadow-lg`
                                    : 'bg-gray-100 dark:bg-neutral-800 text-gray-700 dark:text-neutral-300 hover:bg-gray-200 dark:hover:bg-neutral-700'
                                }`}
                              >
                                {type.label}
                              </button>
                            ))}
                          </div>
                        </div>

                        {/* Location */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <MapPin className="w-4 h-4 text-emerald-500" />
                            Location
                          </label>
                          <input
                            type="text"
                            name="location"
                            value={formData.location || ''}
                            onChange={handleChange}
                            placeholder="e.g., San Francisco, CA / Remote"
                            className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-emerald-500/50 focus:border-emerald-500 transition-all"
                          />
                        </div>

                        {/* Date Range */}
                        <div className="grid grid-cols-2 gap-4">
                          <div className="space-y-2">
                            <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                              <Calendar className="w-4 h-4 text-emerald-500" />
                              Start Date *
                            </label>
                            <input
                              type="date"
                              name="startDate"
                              value={formData.startDate}
                              onChange={handleChange}
                              className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-emerald-500/50 focus:border-emerald-500 transition-all"
                            />
                          </div>
                          <div className="space-y-2">
                            <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                              <Calendar className="w-4 h-4 text-emerald-500" />
                              End Date
                            </label>
                            <input
                              type="date"
                              name="endDate"
                              value={formData.endDate || ''}
                              onChange={handleChange}
                              disabled={formData.isCurrent}
                              className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-emerald-500/50 focus:border-emerald-500 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
                            />
                          </div>
                        </div>

                        {/* Currently Working */}
                        <label className="flex items-center gap-3 cursor-pointer group">
                          <div className="relative">
                            <input
                              type="checkbox"
                              name="isCurrent"
                              checked={formData.isCurrent}
                              onChange={handleCheckboxChange}
                              className="sr-only peer"
                            />
                            <div className="w-5 h-5 border-2 border-gray-300 dark:border-neutral-600 rounded-md peer-checked:bg-emerald-500 peer-checked:border-emerald-500 transition-all flex items-center justify-center">
                              {formData.isCurrent && (
                                <svg className="w-3 h-3 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={3}>
                                  <path strokeLinecap="round" strokeLinejoin="round" d="M5 13l4 4L19 7" />
                                </svg>
                              )}
                            </div>
                          </div>
                          <span className="text-sm text-gray-700 dark:text-neutral-300 group-hover:text-emerald-500 transition-colors">
                            I currently work here
                          </span>
                        </label>

                        {/* Description */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <FileText className="w-4 h-4 text-emerald-500" />
                            Description
                          </label>
                          <textarea
                            name="description"
                            value={formData.description || ''}
                            onChange={handleChange}
                            placeholder="Describe your responsibilities, achievements, and impact..."
                            rows={4}
                            className="w-full px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-emerald-500/50 focus:border-emerald-500 transition-all resize-none"
                          />
                        </div>

                        {/* Skills Used */}
                        <div className="space-y-2">
                          <label className="flex items-center gap-2 text-sm font-medium text-gray-700 dark:text-neutral-300">
                            <Code2 className="w-4 h-4 text-emerald-500" />
                            Skills Used
                          </label>
                          <div className="flex gap-2">
                            <input
                              type="text"
                              value={skillInput}
                              onChange={(e) => setSkillInput(e.target.value)}
                              onKeyPress={handleKeyPress}
                              placeholder="Add skill (press Enter)"
                              className="flex-1 px-4 py-3 bg-gray-50 dark:bg-neutral-800/50 border border-gray-200 dark:border-neutral-700 rounded-xl text-gray-900 dark:text-white placeholder-gray-400 dark:placeholder-neutral-500 focus:outline-none focus:ring-2 focus:ring-emerald-500/50 focus:border-emerald-500 transition-all"
                            />
                            <Button
                              type="button"
                              onClick={addSkill}
                              className="px-4 bg-emerald-500 hover:bg-emerald-600 text-white rounded-xl"
                            >
                              <Plus className="w-5 h-5" />
                            </Button>
                          </div>
                          {formData.skills && formData.skills.length > 0 && (
                            <motion.div
                              initial={{ opacity: 0 }}
                              animate={{ opacity: 1 }}
                              className="flex flex-wrap gap-2 mt-3"
                            >
                              {formData.skills.map((skill) => (
                                <motion.span
                                  key={skill}
                                  initial={{ scale: 0 }}
                                  animate={{ scale: 1 }}
                                  className="inline-flex items-center gap-1.5 px-3 py-1.5 bg-gradient-to-r from-emerald-500/10 to-teal-500/10 border border-emerald-500/20 text-emerald-600 dark:text-emerald-400 rounded-lg text-sm font-medium"
                                >
                                  {skill}
                                  <button
                                    type="button"
                                    onClick={() => removeSkill(skill)}
                                    className="hover:text-red-500 transition-colors"
                                  >
                                    <X className="w-3.5 h-3.5" />
                                  </button>
                                </motion.span>
                              ))}
                            </motion.div>
                          )}
                        </div>
                      </div>

                      {/* Footer */}
                      <div className="flex items-center justify-end gap-3 p-5 border-t border-gray-200/50 dark:border-neutral-800/50 bg-gray-50/50 dark:bg-neutral-800/30">
                        <Button
                          type="button"
                          onClick={onClose}
                          className="px-5 py-2.5 bg-gray-200 dark:bg-neutral-700 hover:bg-gray-300 dark:hover:bg-neutral-600 text-gray-700 dark:text-neutral-300 rounded-xl transition-colors"
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          disabled={saving}
                          className="px-6 py-2.5 bg-gradient-to-r from-emerald-500 to-teal-600 hover:from-emerald-600 hover:to-teal-700 text-white rounded-xl shadow-lg shadow-emerald-500/25 disabled:opacity-50 disabled:cursor-not-allowed transition-all"
                        >
                          {saving ? (
                            <>
                              <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                              Adding...
                            </>
                          ) : (
                            <>
                              <Plus className="w-4 h-4 mr-2" />
                              Add Experience
                            </>
                          )}
                        </Button>
                      </div>
                    </form>
                  </div>
                </motion.div>
              </Dialog.Content>
            </>
          )}
        </AnimatePresence>
      </Dialog.Portal>
    </Dialog.Root>
  );
}
